.testimonial-section{
    padding: 80px 0 80px;
    background-image: url("../../../src/assets/Testimonial/bg-1.jpg");
    background-position: center;
    background-attachment: fixed;
    position: relative;
    z-index: -1;
}
.testimonial-section::before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.85;
    background-color: #1f2235;
}
.footer-image{
    width: 100%;
    height: 100%;
    max-width: 1920px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    margin-top: -110px;
}
.footer-image img{
    max-width: 100%;
    height: 130px;

}
.testimonial-section .testi-item{
    background-color: white;
    padding: 30px;
    border-radius: 50px 50px 50px 0px;

}
.testimonial-section .testi-item p{
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    margin: 0 0 10px;
    font-style: italic;
}
.testimonial-section .testi-item p .fa-quote-left{
    margin-right: 5px;
    color: tomato;
}
.testimonial-section .testi-item p .fa-quote-right{
    margin-left: 5px;
    color: tomato;
}
.testimonial-section .testi-comment .stars{
    margin: 0 0 0 20px;
}
.testimonial-section .testi-comment .stars li{
    display: inline-block;
    margin: 0 1px;
}

.testimonial-section .testi-comment .stars li i{
    font-size: 13px;
    color: tomato;
}

.testimonial-section .client-info{
    position: relative;
    padding-left: 80px;
    padding-top: 5px;
    min-height: 60px;
}
.testimonial-section .client-info img{
    height: 70px;
    width: 70px;
    border: 2px solid transparent;
    border-color: black;
    left: 0;
    top: 0;
    border-radius: 50%;
    position:absolute;
}
.testimonial-section .client-info h5{
    font-size: 16px;
    font-weight: 600;
    line-height: 26px;
    margin: 0 0 2px;
}
.testimonial-section .client-info p{
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    margin: 0;
}
